export default {
	data() {
		return {
			id: '',
			totals: {},
			xpTypes: [],
			list: [],
			pageIndex: 1,
			pageSize: 20,
			pageTotal: 1,
			nodata: false,
			nomore: false
		};
	},
	onLoad(options) {
		this.type = options.type*1
		let name = ''
		switch (this.type) {
			case 1:
				name = '人名币'
				break;
			case 2:
				name = '星票'
				break;
			case 3:
				name = '活跃度'
				break;
			default:
				break;
		}
		uni.setNavigationBarTitle({
			title: name
		})
	},
	onShow() {
		this.setparams()
		this.accountInfo()
		this.getXPTypes()
	},
	methods: {
		setparams() {
			this.pageIndex = 1
			this.nodata = false
			this.nomore = false
			this.list = []
			this.xpTypes = []
		},
		loadMore() {
			if (this.pageIndex < this.pageTotal) {
				this.pageIndex++
				this.getList()
			} else {
				this.nodata = true
			}
		},
		accountInfo() { //1.人名币2.星票3.活跃度
			let self = this
			let params = {
				type: this.type
			}
			this.$acFrame.HttpService.accountNums(params).then(res => {
				if (res.success) {
					self.totals = res.data
				}
			})
		},
		getList() {
			let self = this
			let params = {
				type: this.type,
				pageIndex: this.pageIndex,
				pageSize: this.pageSize
			}
			this.$acFrame.HttpService.accountInfo(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if (list.length > 0) {
						debugger
						list.filter(fv => {
							if (fv.bizTime) {
								fv.bizTime = self.$acFrame.Util.formatTime(fv.bizTime, 'dayhm')
							}
							self.xpTypes.forEach((cv,i) => {
								if (fv.bizType == cv.key) {
									fv.typeName = cv.val
									fv.itemType = cv.extendData
								}
							})
							fv.amount=Math.abs(fv.amount)
						})
						self.list = self.list.concat(list)
					} else {
						self.nodata = true
					}

				}
			})
		},
		getXPTypes() {
			let self = this
			// let xpTypes=uni.getStorageSync('xpTypes')
			// if(xpTypes){
			// 	this.xpTypes = xpTypes
			// 	this.getList()
			// }else{
			if (this.type == 1) {
				this.$acFrame.HttpService.getRMBTypes().then(res => {
					if (res.success) {
						self.xpTypes = res.data
						// uni.setStorageSync('xpTypes', res.data)
						self.getList()
					}
				})
			} else if (this.type == 2) {
				this.$acFrame.HttpService.getXPTypes().then(res => {
					if (res.success) {
						self.xpTypes = res.data
						// uni.setStorageSync('xpTypes', res.data)
						self.getList()
					}
				})
			} else if (this.type == 3) {
				this.$acFrame.HttpService.getActiveTypes().then(res => {
					if (res.success) {
						self.xpTypes = res.data
						// uni.setStorageSync('xpTypes', res.data)
						self.getList()
					}
				})
			}

			// }


		}
	}
}
